<template>
  <div>
      <center>
       <h2><img src="@/assets/images/logo1.png" class="img-rounded" alt="Cinque Terre"></h2>
        <nav class="tabs">
        <div class="selector"></div>
        <a href="javascript:void(0)" class="active"><i class="fa fa-home"></i>Home</a>
        <a href="javascript:void(0)"><i class="fa fa-shopping-basket"></i>Products</a>
        <a href="javascript:void(0)"><i class="fa fa-book"></i>About</a>
        <a href="javascript:void(0)"><i class="fa fa-phone"></i>Contact Us</a>
      </nav>
      </center>
  </div>
</template>

<script>
import $ from 'jquery';

export default {
    name: "navview",
    mounted () {
        var tabs = $('.tabs');
        var activeItem = tabs.find('.active');
        var activeWidth = activeItem.innerWidth();
        $(".selector").css({
            "left": activeItem.position.left + "px", 
            "width": activeWidth + "px"
        });

        $(".tabs").on("click","a",function(e){
            e.preventDefault();
            $('.tabs a').removeClass("active");
            $(this).addClass('active');
            var activeWidth = $(this).innerWidth();
            var itemPos = $(this).position();
            $(".selector").css({
            "left":itemPos.left + "px", 
            "width": activeWidth + "px"
            });
        });
    }
}
</script>

<style lang="scss" src="@/assets/css/nav.scss"></style>